<template>
  <div id="pieChart"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {

  },

  methods: {
    loadChart(arr) {
      this.myChart = echarts.init(document.getElementById("pieChart"));
      this.myChart.setOption(this.initOption(arr));
    },
    init(data){
      var v1 = data.height?data.height:0
      var v2 = data.report?data.report:0
      var v3 = data.fire?data.fire:0
      var v4 = data.ball?data.ball:0
      var v5 = data.other?data.other:0
      var arr = [];
      if(v1){
        arr.push({ value: v1, name: "高度违规" })
      }
      if(v2){
        arr.push({ value: v1, name: "报告词违规" })
      }
      if(v3){
        arr.push({ value: v1, name: "违规发射" })
      }
      if(v4){
        arr.push({ value: v1, name: "球" })
      }
      if(v5){
        arr.push({ value: v1, name: "其他违规" })
      }
      this.loadChart(arr);
    },
    initOption(arr) {
      return {
        title: {
          text: "违规项目统计",
          subtext: "",
          left: "center",
          top: 20,
          textStyle: {
            color: "#05fad5"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        color: ["#20fd82", "#01abf8", "#d8ff00", "#fe6b70","#00ebe7"],
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: "55%",
            center: ["50%", "50%"],
            data: arr,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
#pieChart {
  width: 100%;
  height: 400px;
}
</style>
