<template>
  <section class="data_section" ref="data_section">
    <h3 class="aTitle">违规能力数据分析</h3>
    <div class="headerData">
      <el-select v-model="data.uid" @change="selectGet"  filterable placeholder="请选择人员">
        <el-option v-for="item in options" v-if="item.mode !='1'" :key="item._id" :label="item.name" :value="item._id"></el-option>
      </el-select>
      <el-date-picker
        class="dateWrapper"
        v-model="value1"
        type="datetimerange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
      <el-button type="primary" class="search" @click="search(4)" icon="el-icon-search">查询</el-button>
    </div>
    <el-row :gutter="10" class="row_list order_list">
      <el-col :span="12" class="searchWrapper">
        <el-button class="but1 search" type="primary" @click="search(2)">总体数据</el-button>
        <el-button type="primary" class="search" @click="search(0)">前仓数据</el-button>
        <el-button type="primary" class="search" @click="search(1)">后仓数据</el-button>
      </el-col>
      <el-col :span="5">&nbsp</el-col>
      <el-col :span="6">
        <ul class="df">
          <li>
            <p>违规总次数：</p>
            <p>{{foul.total=="0501421undefined25001undefined20"?2:foul.total}}</p>
          </li>
          <li>
            <p>每进入违规率:</p>
            <p>{{(foul.foulpercent*100).toFixed(2)}}%</p>
          </li>
        </ul>
      </el-col>
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <div class="aTopLeft fTitle">
        <pieChart ref="pieChart"></pieChart>
        <ul class="vioButtom">
          <li>
            <p>
              <i>●</i>
              <span>高度违规</span>
            </p>
            <div>
              <span>{{foul.height?foul.height:0}}</span>
              <span>
                <i>占比</i>
                <i>{{(foul.heightGl*100).toFixed(2)}}%</i>
              </span>
            </div>
          </li>
          <li>
            <p>
              <i>●</i>
              <span>报告词违规</span>
            </p>
            <div>
              <span>{{foul.report?foul.report:0}}</span>
              <span>
                <i>占比</i>
                <i>{{(foul.reportGl*100).toFixed(2)}}%</i>
              </span>
            </div>
          </li>
          <li>
            <p>
              <i>●</i>
              <span>违规发射</span>
            </p>
            <div>
              <span>{{foul.fire?foul.fire:0}}</span>
              <span>
                <i>占比</i>
                <i>{{(foul.fireGl*100).toFixed(2)}}%</i>
              </span>
            </div>
          </li>
          <li>
            <p>
              <i>●</i>
              <span>球</span>
            </p>
            <div>
              <span>{{foul.ball?foul.ball:0}}</span>
              <span>
                <i>占比</i>
                <i>{{(foul.ballGl*100).toFixed(2)}}%</i>
              </span>
            </div>
          </li>
          <li>
            <p>
              <i>●</i>
              <span>其他</span>
            </p>
            <div>
              <span>{{foul.other?foul.other:0}}</span>
              <span>
                <i>占比</i>
                <i>{{(foul.otherGl*100).toFixed(2)}}%</i>
              </span>
            </div>
          </li>
        </ul>
      </div>
      <div class="aTopRight aButtomRight" id="aButtomRight">
        <brokenLine ref="brokenLine"></brokenLine>
      </div>
    </el-row>
  </section>
</template>
<script>
import { foul, trend } from "@/api/foul";
import { formatDate } from "@/utils/mUtils";
import pieChart from "./violation/pieChart";
import brokenLine from "./violation/brokenLine";
export default {
  components: {
    pieChart,
    brokenLine
  },
  data() {
    return {
      value1: "",
      user:this.sessionData("get", "user"),
      data: {
        uid: "",
        name: "",
        site: "2",
        start: "",
        end: ""
      },
      foul: {
        height: 0,
        heightGl: 0,
        ball: 0,
        ballGl: 0,
        fire: 0,
        fireGl: 0,
        report: 0,
        reportGl: 0,
        other: 0,
        otherGl: 0
      },
      options: this.sessionData("get", "userList")
    };
  },
  created() {},
  mounted() {
    this.setFoul(this.user.name,this.data.site);
    this.trend(this.user.name,this.data.site);
  },
  methods: {
    search(s) {
      s = s == 4 ? this.data.site : s;
      var st = this.value1 ? this.value1[0].getTime() : "";
      var et = this.value1 ? this.value1[1].getTime() : "";
      this.setFoul(this.data.name, s , st, et);
      this.trend(this.data.name, s , st, et);
    },
    selectGet(vId) {
      let obj = {};
      obj = this.options.find(item => {
        return item._id === vId;
      });
      this.data.name = obj.name;
    },

    trend(name, site, start, end) {
      trend({
        name: name,
        site: site,
        start: start,
        end: end
      }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "数据查询成功！",
            type: "success"
          });
          var list = this.$unfly(res.data);
          this.$refs.brokenLine.init(list)
        }
      });
    },
    setFoul(name, site, start, end) {
      foul({
        name: name,
        site: site,
        start: start,
        end: end
      }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "数据查询成功！",
            type: "success"
          });
          var list = this.$unfly(res.data);
          list.height = list.height == "050500" ? 0 : list.height;
          list.ball = list.ball == "014200" ? 0 : list.ball;
          var totle =
            list.height + list.ball + list.fire + list.report + list.other;
          list.heightGl = isNaN(list.height / totle)
            ? 0
            : Number((list.height / totle).toFixed(2));
          list.ballGl = isNaN(list.ball / totle)
            ? 0
            : Number((list.ball / totle).toFixed(2));
          list.fireGl = isNaN(list.fire / totle)
            ? 0
            : Number((list.fire / totle).toFixed(2));
          list.reportGl = isNaN(list.report / totle)
            ? 0
            : Number((list.report / totle).toFixed(2));
          list.otherGl = isNaN(list.other / totle)
            ? 0
            : Number((list.other / totle).toFixed(2));

          this.foul = list;
          this.$refs.pieChart.init(list);
        }
      });
    }
  }
};
</script>

