import request from '@/utils/axios'




//获取违规数据
export function foul(params) {
  return request({
    url: '/power/foul',
    method: 'post',
    params
  })
}

//违规数据趋势图
export function trend(params) {
  return request({
    url: '/power/foul/trend',
    method: 'post',
    params
  })
}






