<template>
  <div id="brokenLine6" class="brokenLine6"></div>
</template>

<script>
import echarts from "echarts";
import { formatDate } from "@/utils/mUtils";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {},

  methods: {
    loadChart(title, v1, v2, v3, v4, v5) {
      this.myChart = echarts.init(document.getElementById("brokenLine6"));
      this.myChart.setOption(this.initOption(title, v1, v2, v3, v4, v5));
    },

    init(data) {
      var obj = {};
      for (var j = 0; j < data.length; j++) {
        var y = formatDate(new Date(data[j].start), 5);
        obj[y] = data[j].data;
      }
      var height = [];
      var report = [];
      var fire = [];
      var ball = [];
      var other = [];

      var title = [];
      for (var k in obj) {
        title.push(k);
        if (obj[k]) {
          height.push(obj[k].height ? obj[k].height : 0);
          report.push(obj[k].report ? obj[k].report : 0);
          fire.push(obj[k].fire ? obj[k].fire : 0);
          ball.push(obj[k].ball ? obj[k].ball : 0);
          other.push(obj[k].other ? obj[k].other : 0);
        } else {
          height.push(0);
          report.push(0);
          fire.push(0);
          ball.push(0);
          other.push(0);
        }
      }

      this.loadChart(title, height, report, fire, ball, other);
    },
    initOption(title, v1, v2, v3, v4, v5) {
      return {
        title: {
          text: "违规项目趋势图",
          left: "center",
          top: 10,
          textStyle: {
            color: "#05fad5"
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          left: "center",
          top: 60,
          textStyle: {
            color: "#05fad5",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          },
          data: [
            "高度违规",
            "报告词违规",
            "违规发射",
            "球",
            "其他违规",
            "违规总次数",
            "每进入违规概率"
          ]
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: "3%",
          top: "30%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "#05fad5"
              }
            },
            data: title
          }
        ],
        yAxis: {
          min: 0,
          max: 10,
          name: "次",
          interval: 2,
          axisLine: {
            lineStyle: {
              color: "#05fad5"
            }
          }
        },
        color: ["#1ed674", "#ff6567", "#1c84ff", "#f9ff00", "#ffa9a8"],
        series: [
          {
            name: "高度违规",
            type: "line",
            data: v1
          },
          {
            name: "报告词违规",
            type: "line",
            data: v2
          },
          {
            name: "违规发射",
            type: "line",
            data: v3
          },
          {
            name: "球",
            type: "line",
            data: v4
          },
          {
            name: "其他违规",
            type: "line",
            data: v5
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
.brokenLine6 {
  width: 100%;
  height: 70%;
  float: left;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: hidden;
}
</style>
